/*--------------------------------- FAVORITES/ВЯЗАНЫЕ КОРЗИНКИ ---------------------------------------*/

.favorites {
  margin: auto;
  text-align: left;
  padding-top: 26px;
  position: relative;

  &::after {
    @include pseudo;
    background-image: url("../img/bg-zigzag-line.svg");
    bottom: -43px;
    left: 0;
    height: 6px;
    width: 100%;
    fill: #f9f9f9;
    background-size: 320px 100%;
    background-repeat: repeat-x;
    background-position: center bottom;
    transform: rotate(180deg);
  }
}

.favorites__container {
  @include container;
  position: relative;
}

.favorites__moto {
  display: inline-block;
  font-size: 17px;
  font-weight: 700;
  line-height: 1.18;
  text-transform: uppercase;
  color: #63d1bb;
  margin-top: 7px;
  margin-bottom: 7px;
}

.favorites__discription {
  color: #444444;
  font-size: 15px;
  font-weight: normal;
  line-height: 1.6;
  margin-bottom: 18px;
}

.favorites__spec {
  display: table;
  color: #444444;
  font-size: 15px;
  font-weight: normal;
  line-height: 1.33;
  border: none;
  margin: 0;
  padding: 0;
  text-align: left;
  border-collapse: collapse;
  margin-bottom: 27px;
}

.favorites__value,
.favorites__field {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 27px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.favorites__pic {
  margin-bottom: 20px;
}

.favorites__price {
  display: block;
  font-size: 17px;
  font-weight: 700;
  line-height: 1.41;
  text-align: center;
  color: $color-text-paragraph;
}

.favorites__btn {
  margin-top: 27px;
}

/*--------------------------------- FAVORITES/ВЯЗАНЫЕ КОРЗИНКИ - ПЛАНШЕТ ---------------------------------------*/

@media (min-width: $tablet-width) {
  .favorites {
    padding-top: 46px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    &::after {
      bottom: -68px;
      height: 15px;
      background-size: 768px 100%;
    }
  }

  .favorites__title {
    column-span: all;
    display: flex;
  }

  .favorites__moto {
    display: inline-flex;
    text-align: center;
    width: 76px;
    padding-left: 144px;
    padding-top: 4px;
    line-height: 1;
  }

  .favorites__text {
    display: inline-block;
    width: 310px;
    padding-top: 30px;
  }

  .favorites__discription {
    font-size: 17px;
    line-height: 1.76;
    width: 300px;
  }

  .favorites__spec {
    margin-top: 38px;
    width: 290px;
  }

  .favorites__value {
    padding-right: 0;
  }

  .favorites__field {
    padding-right: 0;
  }

  .favorites__pic {
    position: absolute;
    top: 108px;
    right: 49px;
  }

  .favorites__price {
    display: block;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
    width: 290px;
    margin-top: 31px;
    margin-bottom: 7px;
  }
}

/*--------------------------------- FAVORITES/ВЯЗАНЫЕ КОРЗИНКИ - ДЕСКТОП ---------------------------------------*/

@media (min-width: $desktop-width) {
  .favorites {
    margin-top: 78px;

    &::after {
      bottom: -96px;
      left: 0;
      height: 24px;
      fill: #f1f1f1;
      background-size: 1054px 24px;
      transform: rotate(180deg);
    }
  }

  .favorites__title {
    flex-direction: row-reverse;
    justify-content: flex-end;

    .favorites__heading {
      padding-left: 96px;
      z-index: 2;
    }
  }

  .favorites__moto {
    padding-left: 46px;
    padding-top: 15px;
    margin: 0;
  }

  .favorites__pic {
    top: -51px;
    right: 50px;
    z-index: 1;
  }

  .favorites__text {
    padding-top: 49px;
    margin-left: 174px;
    margin-bottom: 0;
  }

  .favorites__discription {
    margin-bottom: 0;
    margin-top: 0;
  }

  .favorites__spec {
    margin-top: 50px;
  }

  .favorites__price {
    margin-top: 83px;
    margin-left: 175px;
    margin-bottom: 0;
  }

  .favorites__btn {
    margin-left: 175px;
    margin-top: 37px;
  }
}
